@use '../abstracts/variables' as var;

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 10px;
  font-family: var.$font-family2;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1460px;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box;
}

.btn {
  border: none;
  background: transparent;
  outline: none;
  cursor: pointer;
  padding: 0;
}
