@use '../abstracts/variables' as var;

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  transform: translateY(0%);
  transition: 0.3s linear;

  &.hide {
    transform: translateY(-100%);
  }

  &.white-type {
    background: var.$color1;
  }

  &__content {
    padding-top: 6.4rem;
    padding-bottom: 6.4rem;
  }

  &.white-type &__content {
    padding-top: 2.7rem;
    padding-bottom: 2.7rem;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: block;
    cursor: pointer;
    width: 11rem;
    margin-right: 7.9rem;

    img {
      width: 100%;
      height: auto;
    }
  }

  &.white-type &__logo {
    width: 6.9rem;
    margin-right: 5.2rem;
  }

  &.white-type &__logo-img {
    display: none;
  }

  &__logo-img-white  {
    display: none;
  }

  &.white-type &__logo-img-white {
    display: block;
  }

  &.white-type .navigation__control {
    background: var.$color1;

    span {
      background: var.$color2;
      border-radius: 2px;
    }
  }

  &.white-type .header__content > .logotypes {
    .visit-astana-footer {
      display: block;
      width: 10.3rem;
    }
  
    .dev-center-astana-footer {
      display: block;
      width: 11.634rem
    }
  
    .baiterek-astana-footer {
      display: block;
      width: 6.3rem;
    }
  
    .visit-astana,
    .dev-center-astana,
    .baiterek-astana {
      display: none;
    }
  }

  .breadcrumbs {
    width: 100%;
    background: var.$color1;
    border-top: 1px solid var.$color8;
    padding: 2rem 0;
    display: none;

    &.active {
      display: block;
    }

    &__container {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 2rem;
    }

    &__root {
      font-family: var.$font-family2;
      font-size: 1.4rem;
      line-height: 1;
      color: var.$color9;
      font-weight: 500;
    }

    &__childs {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 2rem;

      a {
        font-family: var.$font-family2;
        font-size: 1.4rem;
        line-height: 1;
        color: var.$color2;
        font-weight: 500;
      }
    }
  }
}

.navigation {
  flex-grow: 1;

  &__control {
    width: 62px;
    height: 62px;
    border-radius: 10px;
    background: var.$color3;
    box-sizing: border-box;
    padding: 21.42px 13.75px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

    span {
      width: 100%;
      height: 1.5px;
      background: var.$color1;
      border-radius: 2px;

      &:nth-child(2) {
        width: 80%;
      }

      &:nth-child(3) {
        width: 60%;
      }
    }
  }

  &__body {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    min-height: 100vh;
    box-sizing: border-box;
    padding-top: 10rem;
    padding-bottom: 13.1rem;
    background-color: var.$color3;
    background-image: url('@images/nav-bg.jpg');
    background-size: cover;
    display: flex;
    align-items: stretch;
    transform: translateY(-100%);
    transition: 0.3s linear;

    &.active {
      transform: translateY(0%);
    }

    .container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: stretch;
      flex-grow: 1;
    }
  }

  &__close {
    position: absolute;
    top: 5.3rem;
  }

  &__links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 4rem;
    margin-top: 16.9rem;
  }

  .navigation-links {
    &__title {
      font-family: var.$font-family2;
      font-size: 3.2rem;
      font-weight: 500;
      line-height: 1;
      text-transform: uppercase;
      margin-bottom: 2.2rem;
      color: var.$color1;

      a {
        color: inherit;
      }
    }

    &__item {
      margin-bottom: 1rem;

      &:last-child {
        margin-bottom: 0;
      }

      a {
        font-family: var.$font-family2;
        font-size: 2rem;
        font-weight: 400;
        color: var.$color1;
        transition: 0.3s linear;
      }
    }
  }

  &__bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__socials {
    display: flex;
    gap: 3.5rem;

    a {
      display: block;
      width: 2.4rem;

      img {
        display: block;
        width: 100%;
      }
    }
  }
  
  .logotypes {
    gap: 4.2rem;

    .visit-astana {
      width: 8.5rem;
    }

    .dev-center-astana {
      width: 10.3rem
    }

    .baiterek-astana {
      width: 5.1rem;
    }
  }
}

.logotypes {
  display: flex;
  justify-content: space-between;
  gap: 2.9rem;
  align-items: center;

  &__item {
    display: block;

    img {
      display: block;
    }
  }

  .visit-astana {
    width: 10.3rem;
  }

  .dev-center-astana {
    width: 11.634rem
  }

  .baiterek-astana {
    width: 6.3rem;
  }

  .visit-astana-footer,
  .dev-center-astana-footer,
  .baiterek-astana-footer {
    display: none;
  }
}

.banner {
  height: 968px;
  min-height: 100vh;
  max-height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.footer {
  padding: 6.4rem 0 10.5rem;

  &__top {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    margin-bottom: 6.8rem;

    .logotypes {
      gap: 4.2rem;

      .visit-astana {
        width: 8.5rem;
      }

      .dev-center-astana {
        width: 10.3rem
      }

      .baiterek-astana {
        width: 5.1rem;
      }
    }
  }

  &__logo {
    display: block;
    width: 9.4rem;
    margin-right: 3.3rem;

    img {
      display: block;
      width: 100%;
    }
  }

  &__author {
    align-self: center;
    flex-grow: 1;
    font-size: 1.2rem;
    font-weight: 400;

    span {
      display: block;
      max-width: 32.5rem;
      line-height: 1.4;
    }
  }

  &__bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 8.2rem;
  }

  .links {
    &__title {
      font-family: var.$font-family2;
      font-size: 1.4rem;
      font-weight: 900;
      line-height: 1;
      text-transform: uppercase;
      margin-bottom: 2rem;
      color: var.$color2;

      a {
        color: inherit;
      }
    }

    &__item {
      margin-bottom: 1rem;

      &:last-child {
        margin-bottom: 0;
      }

      a {
        font-family: var.$font-family2;
        font-size: 1.4rem;
        font-weight: 400;
        color: var.$color2;
        transition: 0.3s linear;

        &:hover,
        &:visited {
          color: var.$color3;
        }
      }
    }
  }

  &__socials {
    display: flex;
    align-items: flex-start;
    gap: 3.5rem;

    a {
      display: block;
      width: 2.4rem;

      img {
        display: block;
        width: 100%;
      }
    }
  }
}

.bg-form-1-0 {
  background: 20% 40% / 30% auto no-repeat url('@images/bg-form-1.png');
}
.bg-form-1-1 {
  background: 20% 40% / 30% auto no-repeat url('@images/bg-form-1.png');
}
.bg-form-2-0 {
  background: 20% 40% / 30% auto no-repeat url('@images/bg-form-2.png');
}