@use '../abstracts/variables' as var;

$font-family: Helvetica, sans-serif !default;
$font-family2: Helvetica, sans-serif !default;
$font-size: 16px !default;

body {
  font-family: $font-family;
  font-size: $font-size;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  font-family: $font-family2;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.6rem;
  margin: 0;
}

.banner-title {
  font-family: $font-family2;
  font-size: 10rem;
  font-weight: 800;
  line-height: 12rem;
  color: var.$color1;
  text-transform: uppercase;
}

.banner-subtitle {
  font-family: $font-family;
  font-size: 6.4rem;
  font-weight: 400;
  line-height: 7.8rem;
  color: var.$color1;
}

.title-1 {
  font-family: $font-family2;
  font-size: 6.4rem;
  font-weight: 800;
  line-height: 7.7rem;
  color: var.$color2;
  text-transform: uppercase;

  &--thin {
    font-weight: 400;
  }
}

.title-2 {
  font-family: $font-family2;
  font-size: 4.8rem;
  font-weight: 700;
  line-height: 5.8rem;
}

.title-3 {
  font-family: $font-family2;
  font-size: 3.2rem;
  font-weight: 400;
  line-height: 4.5rem;
}

.title-4 {
  font-family: $font-family;
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 2.9rem;
}

.title-5 {
  font-family: $font-family2;
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 2.9rem;
}

.title-6 {
  font-family: $font-family2;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
}
